.ToDosListItem {
	background-color: #eee;
	border: 1px solid #ccc;
	border-top-style: hidden;
	display: -ms-flexbox;
	display: flex;
	height: 50px;
}

.ToDosListItem-checkbox {
	-ms-flex-preferred-size: 50px;
	    flex-basis: 50px;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	background: url(../images/circle.svg) no-repeat center;
	background-size: 60%;
}

.ToDosListItem-checkbox.checked {
	background: url(../images/checked.svg) no-repeat 75% 35%, url(../images/circle.svg) no-repeat center;
	background-size: 60%;
}

.ToDosListItem-checkbox:active {
	background-size: 58%;
}

.ToDosListItem-task {
	-ms-flex-positive: 1;
	    flex-grow: 1;
	color: #222;
	padding: 15px;
	font-size: 20px;
}

.ToDosListItem-task.edited {
	background-color: #f5f5f5;
	width: 100%;
	border: 1px solid #aaa;
	outline: none;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.ToDosListItem-task.showed {
	line-height: 100%;
}

.ToDosListItem-task.showed.completed {
	color: #aaa;
	text-decoration: line-through;
}

.ToDosListItem-button {
	-ms-flex-preferred-size: 50px;
	    flex-basis: 50px;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	background: url(../images/cancel.svg) no-repeat center;
	background-size: 40%;
	outline: none;
	border: none;
}

.ToDosListItem-button:active {
	background-size: 38%;
}



